        function handleReferee(event){
            //(e) => this.doSearch(e)
            let k = event.target.value
            //setRefID(k)
            //return;
            a(`onChange: ${k}`)
            if(k.trim().length == 0){
                a(1)
                setRefID("0")
                setShowRefID("0")
            } 
            if(k == 0){
                setRefID("0")
                setShowRefID(k)
            }
            else if(k>0){
                a(2)
                setRefID(k)
                setShowRefID(k)
            }
                
            else{
                a(3)
                setRefID("0")
                setShowRefID("")
            }

        }

